<template>
  <div class="my-container">
    <div class="header user-info" v-if="user">
      <div class="base-info">
        <div class="left">
          <van-image
            class="avatar"
            :src="userInfo.photo"
            round
            fit="cover"
          />
          <span class="name">{{ userInfo.name }}</span>
        </div>
        <div class="right">
          <van-button size="mini" round class="mini" to="/user">编辑资料</van-button>
        </div>
      </div>
      <div class="hobby-info">
        <van-grid>
          <van-grid-item text="头条">
            <template #icon>
              <div>{{ userInfo.art_count }}</div>
            </template>
          </van-grid-item>
          <van-grid-item text="关注">
            <template #icon>
              <div>{{ userInfo.follow_count}}</div>
            </template>
          </van-grid-item>
          <van-grid-item text="粉丝">
            <template #icon>
              <div>{{userInfo.fans_count}}</div>
            </template>
          </van-grid-item>
          <van-grid-item text="获赞">
            <template #icon>
              <div>{{userInfo.like_count}}</div>
            </template>
          </van-grid-item>
        </van-grid>
      </div>
    </div>

    <div class="header not-login" v-else>
      <div class="login-btn" @click="$router.push('/login')">
        <img class="mobile-img" src="~@/assets/mobile.png" alt="" />
        <span class="text">登录 / 注册</span>
      </div>
    </div>

    <!-- 宫格导航 -->
    <van-grid class="grid-nav mb-10" :column-num="2">
      <van-grid-item text="收藏">
        <template #icon>
          <i slot="icon" class="toutiao toutiao-shoucang"></i>
        </template>
      </van-grid-item>
      <van-grid-item text="历史">
        <template #icon>
          <i slot="icon" class="toutiao toutiao-lishi"></i>
        </template>
      </van-grid-item>
    </van-grid>

    <van-cell title="消息通知" is-link></van-cell>
    <van-cell title="小智同学" is-link class="mb-10"></van-cell>
    <van-cell
      v-if="user"
      title="退出登录"
      class="logout-cell"
      @click="onLogout()"
      clickable
    ></van-cell>
  </div>
</template>

<script>
import { getUserInfoAPI } from '../../api/index.js'
import { mapState } from 'vuex'
export default {

  name: 'myIndex',
  data () {
    return {
      userInfo: {}
    }
  },
  created () {
    if (this.user) {
      this.loadUserInfo()
    }
  },
  computed: {
    ...mapState(['user'])
  },
  methods: {
    async loadUserInfo () {
      try {
        const res = await getUserInfoAPI()
        console.log(res.data.data)
        this.userInfo = res.data.data
      } catch (error) {
        this.$toast('获取数据失败')
      }
    },
    onLogout () {
      // 退出提示
      // 在组件中需要使用 this.$dialog 来调用弹框组件
      this.$dialog.confirm({
        title: '确认退出吗？'
      }).then(() => {
        // on confirm
        // 确认退出：清除登录状态（容器中的 user + 本地存储中的 user）
        this.$store.commit('setUser', null)
      }).catch(() => {
        // on cancel
        console.log('取消执行这里')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.my-container {
  padding-bottom: 100px;
  .header {
    min-height: 361px;
    background: url("../../assets/banner.png");
    // background: url("~@/assets/banner.png");
    background-size: cover;
  }
  .base-info {
    height: 230px;
    padding: 76px 30px 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .left {
      display: flex;
      align-items: center;
      .avatar {
        width: 130px;
        height: 130px;
        border: 4px solid #fff;
        margin-right: 24px;
      }

      .name {
        font-size: 28px;
        color: #fff;
      }
    }
    .right {
      .mini {
        font-size: 14px;
        color: #999;
      }
    }
  }
  .hobby-info {
    [class*="van-hairline"]::after {
      border: unset;
    }
    /deep/.van-grid-item__content::after {
      border: unset;
    }
    /deep/.van-grid-item__content {
      background-color: unset;
      color: #fff;
      .van-grid-item__text {
        color: #fff;
      }
    }
  }
  .not-login {
    display: flex;
    justify-content: center;
    align-items: center;
    .login-btn {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .mobile-img {
        width: 132px;
        height: 132px;
        margin-bottom: 15px;
      }
      .text {
        font-size: 28px;
        color: #fff;
      }
    }
  }
  .grid-nav {
    .toutiao {
      font-size: 45px;
    }
    .toutiao-shoucang {
      color: #eb5253;
    }
    .toutiao-lishi {
      color: #ff9d1d;
    }
    /deep/.van-grid-item__text {
      font-size: 30px;
    }
  }
  .logout-cell {
    text-align: center;
    color: #eb5253;
  }
}
</style>
